/* global window */
import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { Checkbox } from '@material-ui/core';
import PropTypes from 'prop-types';
import LoadingCircle from '../../../../components/LoadingCircle';
import styles from './styles';

const BlockMember = ({ classes, blockedAt, loading, buildingName, onConfirmBlock }) => (
  <div>
    {
      loading ?
        <div className={classes.checkBoxLoadingView}>
          <div className={classes.loading}>
            <LoadingCircle />
          </div>
          <small>
            Desativar acesso
            <br />
            {`(${buildingName})`}
          </small>
        </div>
        :
        <label className={classes.checkBoxView}>
          <Checkbox
            checked={blockedAt}
            style={styles.checkbox}
            onChange={() => {
              if (window.confirm('Tem certeza em realizar essa ao?')) {
                onConfirmBlock();
              }
            }}
          />
          <small>
            Desativar acesso
            <br />
            {`(${buildingName})`}
          </small>
        </label>
    }
  </div>
);

BlockMember.propTypes = {
  loading: PropTypes.bool,
  onConfirmBlock: PropTypes.func,
  classes: PropTypes.object,
  blockedAt: PropTypes.bool,
  buildingName: PropTypes.string
};

export default withStyles(styles, { withTheme: true })(BlockMember);
