import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _get from 'lodash/get';
import _map from 'lodash/map';
import _sortBy from 'lodash/sortBy';
import _mapValues from 'lodash/mapValues';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@material-ui/core/Button';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import { compose, withState, withHandlers } from 'recompose';
import MomentUtils from '@date-io/moment';
import moment from 'moment';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import TicketsService from '../../services/tickets';
import TicketCategoriesService from '../../services/ticket-categories';
import withRequest from '../../utils/with-request';
import Session from '../../utils/session';
import TicketsEnum from '../../enums/tickets';
import config from '../../config';
import styles from './styles';

const { PRIORITIES } = TicketsEnum;

const emptyTicket = () => ({
  title: '',
  description: '',
  category_id: '',
  deadline: null,
  priority: _get(PRIORITIES, '[0].key'),
  building_id: _get(Session.getUserData(), 'buildings[0].id')
});

const NewTicket = memo(({
  classes, alert, setAlert, ticket, setTicketState,
  ticketLoading, ticketError, ticketFetch,
  ticketCategoriesLoading, ticketCategoriesData
}) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const enterpriseUser = Session.getUserData();
  const buildings = _sortBy(_get(enterpriseUser, 'buildings', []), ['name']);
  const categories = _get(ticketCategoriesData, 'ticket_categories', []);
  const errors = _mapValues(_get(ticketError, 'errors', {}), (err) => err[0]);
  const isDisabled = ticketLoading || ticketCategoriesLoading;
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <aside>
              <Typography variant="h5">
                <b>Novo ticket</b>
              </Typography>
            </aside>
          </div>
        </div>
        <br />
        <div className={classes.container}>
          <Paper className={classes.paper}>
            <Typography variant="h6">
              <b>Solicitao</b>
            </Typography>
            <Grid container item xs={12} sm={8} md={6} spacing={2}>
              <Grid item xs={12}>
                <FormControl fullWidth error={!!errors.title}>
                  <TextField
                    label="* Ttulo"
                    error={!!errors.title}
                    className={classes.input}
                    disabled={isDisabled}
                    placeholder="Ex.: Banheiro entupido, mquina de caf com defeito, etc"
                    value={_get(ticket, 'title')}
                    onChange={e => setTicketState({ title: e.target.value })}
                    variant="outlined"
                    InputLabelProps={{ shrink: true }}
                  />
                  {errors.title && <FormHelperText>{errors.title}</FormHelperText>}
                </FormControl>
                <FormControl fullWidth error={!!errors.description}>
                  <TextField
                    label="Descrio"
                    error={!!errors.description}
                    className={classes.input}
                    disabled={isDisabled}
                    placeholder="Mais detalhes sobre a ocorrncia..."
                    value={_get(ticket, 'description')}
                    onChange={e => setTicketState({ description: e.target.value })}
                    variant="outlined"
                    InputLabelProps={{ shrink: true }}
                  />
                  {errors.description && <FormHelperText>{errors.description}</FormHelperText>}
                </FormControl>
                <FormControl fullWidth error={!!errors.building_id}>
                  <TextField
                    select
                    label="* Prdio"
                    error={!!errors.building_id}
                    className={classes.input}
                    disabled={isDisabled}
                    value={_get(ticket, 'building_id')}
                    SelectProps={{ native: true }}
                    InputLabelProps={{ shrink: true }}
                    onChange={e => setTicketState({ building_id: e.target.value })}
                    variant="outlined"
                  >
                    {buildings.map(b => <option key={b.id} value={b.id}>{b.name}</option>)}
                  </TextField>
                  {errors.building_id && <FormHelperText>{errors.building_id}</FormHelperText>}
                </FormControl>
                <FormControl fullWidth error={!!errors.category_id}>
                  <TextField
                    select
                    label="* Categoria"
                    error={!!errors.category_id}
                    className={classes.input}
                    disabled={isDisabled}
                    value={_get(ticket, 'category_id')}
                    SelectProps={{ native: true }}
                    InputLabelProps={{ shrink: true }}
                    onChange={e => setTicketState({ category_id: e.target.value })}
                    variant="outlined"
                  >
                    {_map(categories, c => <option key={c.id} value={c.id}>{c.title}</option>)}
                  </TextField>
                  {errors.category_id && <FormHelperText>{errors.category_id}</FormHelperText>}
                </FormControl>
              </Grid>
            </Grid>
            <br />
            <Typography variant="h6">
              <b>Deadline</b>
            </Typography>
            <Grid container item xs={12} sm={8} md={6} spacing={2}>
              <Grid item xs={12} sm={6}>
                <FormControl fullWidth>
                  <MuiPickersUtilsProvider
                    libInstance={moment}
                    utils={MomentUtils}
                    locale={config.locale}
                  >
                    <DatePicker
                      autoOk
                      label="Prazo"
                      clearable
                      animateYearScrolling
                      className={classes.input}
                      disabled={isDisabled}
                      invalidDateMessage="Formato invlido"
                      variant="outlined"
                      inputVariant="outlined"
                      InputLabelProps={{ shrink: true }}
                      value={_get(ticket, 'deadline')}
                      onChange={e => setTicketState({ deadline: e })}
                      minDate={new Date()}
                      format="DD/MM/YYYY"
                      cancelLabel="Cancelar"
                      clearLabel="Limpar"
                    />
                  </MuiPickersUtilsProvider>
                </FormControl>
              </Grid>
              <Grid item xs={12} sm={6}>
                <FormControl fullWidth error={!!errors.priority}>
                  <TextField
                    select
                    label="Prioridade"
                    error={!!errors.priority}
                    className={classes.input}
                    disabled={isDisabled}
                    value={_get(ticket, 'priority')}
                    InputLabelProps={{ shrink: true }}
                    SelectProps={{ native: true }}
                    onChange={e => setTicketState({ priority: e.target.value })}
                    variant="outlined"
                  >
                    {PRIORITIES.map(p => <option value={p.key} key={p.key}>{p.label}</option>)}
                  </TextField>
                  {errors.priority && <FormHelperText>{errors.priority}</FormHelperText>}
                </FormControl>
              </Grid>
              <Grid item xs={12}>
                <div className={classes.actions}>
                  <Button
                    component={Link}
                    to="/tickets"
                    color="default"
                    size="large"
                    disabled={isDisabled}
                    className={classes.cancel}
                  >
                    Cancelar
                  </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    size="large"
                    disabled={isDisabled}
                    onClick={() => ticketFetch(true).catch(() => {})}
                  >
                    {isDisabled ? 'Aguarde...' : 'Enviar'}
                  </Button>
                </div>
              </Grid>
            </Grid>
          </Paper>
        </div>
      </section>
    </div>
  );
});

NewTicket.propTypes = {
  classes: PropTypes.object,
  ticket: PropTypes.object,
  ticketError: PropTypes.object,
  ticketFetch: PropTypes.func,
  ticketLoading: PropTypes.bool,
  ticketCategoriesData: PropTypes.object,
  ticketCategoriesLoading: PropTypes.bool,
  setTicketState: PropTypes.func,
  alert: PropTypes.object,
  setAlert: PropTypes.func
};

const withTicketCategories = withRequest(TicketCategoriesService.getCategories, {
  name: 'ticketCategories',
  options: ({ setTicketState }) => ({
    onRequestFinished: (data) => {
      setTicketState({ category_id: _get(data, 'ticket_categories[0].id') });
    }
  })
});

const withNewTicket = withRequest(TicketsService.postTicket, {
  name: 'ticket',
  init: false,
  options: ({ ticket, setTicket, setAlert, ticketCategoriesData }) => ({
    variables: ticket,
    onRequestFinished: () => {
      setAlert({ alertMsg: 'Ticket registrado com sucesso', alertType: 'success', showAlert: true });
      setTicket({ ...emptyTicket(), category_id: _get(ticketCategoriesData, 'ticket_categories[0].id') });
    },
    onRequestError: () => {
      setAlert({ alertMsg: 'Erro ao registrar esse ticket', alertType: 'error', showAlert: true });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withState('ticket', 'setTicket', emptyTicket()),
  withHandlers({
    setTicketState: ({ setTicket, ticket }) => (attrs) => {
      setTicket({ ...ticket, ...attrs });
    }
  }),
  withTicketCategories,
  withNewTicket
)(NewTicket);
