import React, { memo } from 'react';
import PropTypes from 'prop-types';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Paper from '@material-ui/core/Paper';
import _findIndex from 'lodash/findIndex';
import { withRouter } from 'react-router';
import { compose, withState, lifecycle } from 'recompose';
import Routes from '../../routes';
import styles from './styles';

const tabMenu = Object
  .values(Routes)
  .filter((r) => !r.hideMenu && r.settingsMenu);

const SettingsMenu = memo(({ history, tabIndex, setTabIndex }) => (
  <Paper square>
    <Tabs
      value={tabIndex}
      indicatorColor="primary"
      textColor="primary"
      onChange={(e, newTabIndex) => setTabIndex(newTabIndex)}
    >
      {tabMenu.map((t, index) => (
        <Tab
          key={t.path}
          label={t.label}
          index={index}
          style={styles.tab}
          component="a"
          onClick={e => {
            e.preventDefault();
            history.push(t.path);
          }}
        />
      ))}
    </Tabs>
  </Paper>
));

SettingsMenu.propTypes = {
  history: PropTypes.object,
  tabIndex: PropTypes.number,
  setTabIndex: PropTypes.func
};

export default compose(
  withRouter,
  withState('tabIndex', 'setTabIndex', 0),
  lifecycle({
    componentDidMount() {
      const { history, setTabIndex } = this.props;
      setTabIndex(_findIndex(tabMenu, t => history.location.pathname === t.path));
    }
  })
)(SettingsMenu);
